@font-face {
  font-family: "Trebuchet";
  src: url("../../../build/css/fonts/TrebuchetMS.ttf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "BebasNeue";
  src: url("../../../build/css/fonts/BebasNeue.otf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "WebSymbols";
  src: url("../../../build/css/fonts/websymbols-regular-webfont.svg#WebSymbols") format("svg");
  src: url("../../../build/css/fonts/websymbols-regular-webfont.eot"), url("../../../build/css/fonts/websymbols-regular-webfont.eot?#iefix"), url("../../../build/css/fonts/websymbols-regular-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
body {
  background: url("../../../build/img/pattern.png");
}

.wrap {
  width: 100%;
}
@media (min-width: 768px) {
  .wrap {
    width: 90%;
    margin: 0px auto;
  }
}
@media (min-width: 1200px) {
  .wrap {
    width: 1200px;
  }
}

.new-wrap {
  width: 100%;
  margin: 0px auto;
}

.icon {
  font-family: "WebSymbols";
  margin-top: 0px;
}

.clearfix {
  float: none;
  clear: both;
}

/*Animations*/
@keyframes rotate-gear {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}
@keyframes fromBellow {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(200%);
  }
  100% {
    transform: translateY(0%);
  }
}
@keyframes fromRight {
  from {
    transform: translateX(200%);
  }
  to {
    transform: translateX(0%);
  }
}
@keyframes backBanner {
  0% {
    background-position: 0px 0px;
  }
  100% {
    background-position: -200px -200px;
  }
}
@keyframes showText {
  0% {
    transform: translateX(-600%) scale(5, 5);
    opacity: 1;
  }
  50% {
    transform: translateX(600%) scale(5, 5);
    opacity: 1;
  }
  75% {
    transform: translateX(-600%) scale(5, 5);
    text-shadow: none;
    color: transparent;
    opacity: 0;
  }
  100% {
    transform: translateX(0%) scale(1, 1);
    text-shadow: 2px 2px 1px #000;
    color: #fff;
    opacity: 1;
  }
}
@keyframes showIcon {
  from {
    transform: translateY(-200%);
  }
  to {
    transform: translateY(0%);
  }
}
@keyframes showCategory {
  from {
    transform: translateY(-300%);
    color: #000;
  }
  to {
    transform: translateY(0%);
    color: #fff;
  }
}
@keyframes showDescription {
  from {
    transform: translateX(-300%);
    color: #000;
  }
  to {
    transform: translateX(0%);
    color: #fff;
  }
}
@keyframes blink {
  0% {
    border: 10px solid #37bcf9;
  }
  25% {
    border: 10px solid green;
  }
  50% {
    border: 10px solid yellow;
  }
  75% {
    border: 10px solid #fff;
  }
  100% {
    border: 10px solid #37bcf9;
  }
}
header {
  width: 100%;
  height: 300px;
  background-color: #000;
  font-family: "BebasNeue";
  color: #fff;
}
@media (min-width: 768px) {
  header {
    height: 70px;
  }
}
header #logo {
  display: none;
  float: left;
  width: 200px;
  height: 55px;
  margin-top: 8px;
  margin-left: 20px;
  background-color: #37bcf9;
  text-align: center;
  letter-spacing: 1px;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s;
}
header #logo:hover {
  border-radius: 2px;
  color: #000;
  background: #ccc;
}
header #logo:hover .gear {
  animation: fromBellow 0.5s linear;
}
header #logo:hover h3 {
  animation: fromRight 0.3s linear;
}
header #logo .gear {
  font-family: "WebSymbols";
  display: block;
  float: left;
  font-size: 30px;
  margin-top: 8px;
  margin-left: 26px;
  /*animation*/
  animation-name: rotate-gear;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
header #logo h3 {
  display: block;
  font-size: 40px;
  float: right;
  margin-top: 5px;
  margin-right: 65px;
  transition: all 0.3s;
}
@media (min-width: 768px) {
  header #logo {
    display: block;
  }
}
header nav ul {
  height: 70px;
  margin-top: 0px;
  padding-left: 0px;
  list-style: none;
}
@media (min-width: 768px) {
  header nav ul {
    float: right;
  }
}
header nav ul li {
  text-align: center;
  font-size: 24px;
  display: block;
  margin: 0px 20px;
  letter-spacing: 1px;
  line-height: 70px;
}
@media (min-width: 768px) {
  header nav ul li {
    display: inline-block;
  }
}
header nav ul li a {
  display: block;
  color: #fff;
  text-decoration: none;
  transition: all 0.2s;
}
header nav ul li a:hover {
  color: #37bcf9;
}
@media (min-width: 768px) {
  header nav ul li a:hover {
    transform: scale(1.2, 1.2);
  }
}

#info #banner {
  width: 80%;
  height: 100px;
  border: 5px solid #fff;
  box-shadow: 0px 0px 2px gray;
  background: url("../../../build/img/bakbaner.png");
  background-position: 0px 0px;
  margin: 20px auto;
  animation: backBanner 10s linear;
  overflow: hidden;
}
@media (min-width: 768px) {
  #info #banner {
    background-position: -200px -200px;
    height: 100px;
    width: 95%;
    border: 10px solid #fff;
  }
}
#info #banner h1 {
  display: block;
  width: auto;
  font-family: "BebasNeue";
  color: #fff;
  font-size: 25px;
  text-shadow: 2px 2px 1px #000;
  text-align: center;
  margin: 37px auto;
}
@media (min-width: 768px) {
  #info #banner h1 {
    animation: showText 10s linear;
    letter-spacing: 2px;
    margin: 33px auto;
    font-size: 35px;
  }
}
@media (min-width: 1200px) {
  #info #banner h1 {
    font-size: 45px;
    margin: 27px auto;
  }
}

#cards {
  width: 80%;
  margin: 0px auto;
  text-align: center;
}
@media (min-width: 768px) {
  #cards {
    padding-left: 50px;
  }
}
@media (min-width: 1200px) {
  #cards {
    padding-left: 0px;
  }
}
#cards .card {
  display: inline-block;
  width: 100px;
  height: 100px;
  padding: 5px;
  background: #fff;
  overflow: hidden;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 2px #ddd;
  cursor: pointer;
  transition: all 0.3s;
  margin-top: 5px;
}
@media (min-width: 768px) {
  #cards .card {
    float: left;
    width: 205px;
    height: 305px;
    margin: 10px 5px;
  }
}
@media (min-width: 1200px) {
  #cards .card {
    margin: 40px 16px 20px;
  }
}
#cards .card .icon {
  display: block;
  width: 100%;
  line-height: 55px;
  font-size: 30px;
  text-align: center;
  margin: 0px auto;
  transition: all 0.5s;
}
@media (min-width: 768px) {
  #cards .card .icon {
    height: 130px;
    line-height: 130px;
    font-size: 50px;
  }
}
#cards .card .category {
  width: 100%;
  font-family: "Trebuchet";
  font-size: 12px;
  font-weight: bold;
  text-align: center;
  color: #00538c;
  margin-bottom: 0px;
  transition: all 0.5s;
}
@media (min-width: 768px) {
  #cards .card .category {
    height: 50px;
    font-size: 21px;
  }
}
#cards .card .description {
  display: none;
  margin: 0px 10px;
  text-align: center;
  color: #0677c2;
  font-family: "Trebuchet";
  transition: all 0.3s;
}
@media (min-width: 768px) {
  #cards .card .description {
    display: block;
  }
}
#cards .card:hover {
  background-color: #000;
}
#cards .card:hover .icon {
  font-size: 70px;
  color: #37bcf9;
  animation: showIcon 0.3s linear;
}
#cards .card:hover .category {
  color: white;
  animation: showCategory 0.4s linear;
}
#cards .card:hover .description {
  animation: showDescription 0.4s linear;
  color: #fff;
}

