  font-family: "BebasNeue";
  font-size: 40px;
  letter-spacing: 3px;
  text-decoration: none;
  color: #00538c;
}
section #articles #blog-linker:hover {
  border: 10px solid #37bcf9;
  border-radius: 240px;
  box-shadow: 0px 0px 10px gray;
  background-color: #333;
  animation: blink 10s infinite linear;
}
section #articles #blog-linker:hover a {
  text-shadow: 1px 1px 1px #000, 1px 2px 1px #000, 1px 3px 1px #000, 1px 4px 1px #000, 1px 5px 1px #000;
  color: white;
}

footer {
  width: 100%;
  height: 800px;
  background: #000000;
  color: white;
  padding-bottom: 20px;
  padding-top: 20px;
}
@media (min-width: 768px) {
  footer {
    padding-top: 20px;
  }
}
@media (min-width: 1200px) {
  footer {
    padding: 20px;
    height: 470px;
  }
}
footer > div > div {
  color: #fff;
  font-family: "Trebuchet";
  text-align: center;
}
@media (min-width: 1200px) {
  footer > div > div {
    float: left;
  }
}
footer .menu_footer {
  width: 100%;
}
@media (min-width: 1200px) {
  footer .menu_footer {
    width: 10%;
    margin-right: 20px;
  }
}
footer .location, footer .info {
  width: 100%;
}
@media (min-width: 768px) {
  footer .location, footer .info {
    width: 40%;
  }
}
footer .location {
  float: none;
}
@media (min-width: 768px) {
  footer .location {
    float: left;
  }
}
@media (min-width: 1200px) {
  footer .location {
    margin-left: 40px;
  }
}
footer nav ul {
  text-align: left;
  padding: 0px;
  list-style: none;
}
footer nav ul li {
  margin-top: 10px;
  border-bottom: 1px solid #ccc;
}
footer nav ul li:hover a {
  padding-left: 40px;
}
footer nav a {
  display: block;
  height: 40px;
  text-decoration: none;
  font-size: 25px;
  color: #37bcf9;
  transition: all 0.3s;
}
footer h4 {
  display: block;
  background: url("../../../build/img/pxgray.png"), #fff;
  height: 50px;
  margin-bottom: 30px;
  letter-spacing: 2px;
  font-family: "BebasNeue";
  font-size: 30px;
  line-height: 50px;
  color: black;
}
@media (min-width: 1200px) {
  footer h4 {
    font-size: 40px;
  }
}
footer iframe {
  width: 80%;
  height: 300px;
  margin-top: 10px;
  border-radius: 5px;
  border: 5px solid white;
  box-shadow: 0px 0px 5px gray;
}
footer .info {
  float: none;
}
@media (min-width: 768px) {
  footer .info {
    float: right;
  }
}
footer .info .browser {
  width: 45px;
}
footer .min-fix {
  margin-top: 15px;
  margin-bottom: 10px;
}
footer .footer-logo {
  width: 180px;
}